class App extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      JobArray: JobArray,
      FilterArray: []
    }
    this.addFilter = this.addFilter.bind(this)
    this.clearFilters = this.clearFilters.bind(this)
    this.removeFilter = this.removeFilter.bind(this)
    this.trimResults = this.trimResults.bind(this)
    this.compareTags = this.compareTags.bind(this)
  }

  trimResults(filter) {
    let trimmedArray = this.state.JobArray.filter(job => {
      return job.tags.includes(filter)
    })
    this.setState({JobArray: trimmedArray})
  }

  addFilter(e) {
    let newArray = this.state.FilterArray;
    let filter = e.target.innerText;
    if(!newArray.includes(filter)){
      newArray.push(filter)
      this.setState({FilterArray: newArray})
      this.trimResults(filter)
    }
  }

  compareTags() {
    let newerArray = []
    if(this.state.FilterArray.length === 0){
     return this.setState({JobArray: JobArray})
    }

    for(let i = 0; i < JobArray.length; i++) {
      let currentJob = JobArray[i].tags;
      if(this.state.FilterArray.every(filter => currentJob.includes(filter))) {
        newerArray.push(JobArray[i])
      }
    } this.setState({JobArray: newerArray})
  }

  removeFilter(e) {
    let newArray = this.state.FilterArray;
    let filter = e.target.parentElement.children[0].innerText;
    let index = newArray.findIndex(index => index === filter)
    newArray.splice(index, 1)
    this.setState({FilterArray: newArray})
    this.compareTags()
  }

  clearFilters() {
    this.setState({FilterArray: []})
    this.setState({JobArray: JobArray})
  }

  render() {

  return (
    <div className="App">
      <div className="header"></div>
      <ActiveTags FilterArray={this.state.FilterArray}
                  clearFilters={this.clearFilters}
                  removeFilter={this.removeFilter}/>
      <Joblist JobArray={this.state.JobArray}
               addFilter={this.addFilter}/>
    </div>
  );
}

}

export default App;
