import React from 'react';
import iconRemove from './icon-remove.svg';

class ActiveTag extends React.Component{



    render() {

        return(
            <div className="tag">
                <span className="tag__name">{this.props.tag}</span>
                <img className="tag__remove" src={iconRemove} alt ="Close icon" onClick={this.props.removeFilter}></img>
            </div>
        )
    }
}

export default ActiveTag;