import React from 'react';
import ActiveTag from '../ActiveTag/ActiveTag'


function NoFilters(props) {
    return null;
}

function Filters(props) {
    return(
        <div className="active-tags">
            <div className="active-tags__inner">
              <div className="active-tags__inner__tags">
                {
                    props.FilterArray.map(tag => {
                        return <ActiveTag key={`${tag}-active`}
                                          tag={tag}
                                          removeFilter={props.removeFilter}/>
                    })
                }
              </div>
              <span className="active-tags__inner__clear" onClick={props.clearFilters}>Clear</span>
            </div>
        </div>
        )
}



class ActiveTags extends React.Component{

    tagsRenderer() {
        if(this.props.FilterArray.length > 0) {
          return <Filters FilterArray={this.props.FilterArray}
                          clearFilters={this.props.clearFilters}
                          removeFilter={this.props.removeFilter}/>
        } else {
          return <NoFilters />
        }
      }

    render() {

    return this.tagsRenderer()
    }
}

export default ActiveTags;