import React from 'react';
import JobTag from '../JobTag/JobTag';

class Job extends React.Component{


    checkIfNew() {
        if(this.props.job.time[0] <= 2 && this.props.job.time[1] === 'd') {
            return(
                <span className="job__details__text__company__new">New!</span>
            )
        }
    }
    render() {
        let className = "job";
        if(this.props.job.isFeatured){
            className += ' job--featured'
        }
        return(
            <div className={className}>

                <div className="job__details">
                    <img className="job__details__logo" src={this.props.job.image} alt="Company Logo"/>
                    <div className="job__details__text">
                        <div className="job__details__text__company">
                            <span className="job__details__text__company__name">{this.props.job.company}</span>
                            {this.checkIfNew()}
                            <span className="job__details__text__company__featured">Featured</span>
                        </div>
                        <span className="job__details__text__position">{this.props.job.position}</span>
                        <div className="job__details__text__bottom">
                            <span className="job__details__text__bottom__time">{this.props.job.time}</span>
                            <div className="job__details__text__bottom__filler"></div>
                            <span className="job__details__text__bottom__status">{this.props.job.contract}</span>
                            <div className="job__details__text__bottom__filler"></div>
                            <span className="job__details__text__bottom__location">{this.props.job.location}</span>
                        </div>
                    </div>
                </div>

                <div className="job__tags">
                   {
                       this.props.job.tags.map(tag => {
                           return <JobTag key={`${this.props.job.id}-${tag}`}
                                          tag={tag}
                                          addFilter={this.props.addFilter}/>
                       })
                   }
                </div>
            
            </div>
        )
    }
}

export default Job;